import React, {Component} from 'react';
import {Badge, Button, Table} from "react-bootstrap";
import moment from "moment";
import Constant from "../../constants/Constant.json";
import {setToken} from "../../../../redux/user/user.actions";
import {connect} from "react-redux";

class VaccineSessionTable extends Component {
    state = {
        week: [0 ,1, 2, 3, 4, 5, 6].map(it=> moment().add(it, 'day').format("DD-MM-YY")),
        filters: [{
            text: "Age 18-45",
            value: 18,
            active: false,
        }, {
            text: "Age 45+",
            value: 45,
            active: false,
        }, {
            text: "Covaxin",
            value: "COVAXIN",
            active: false,
        }, {
            text: "Covishield",
            value: "COVISHIELD",
            active: false,
        }, {
            text: "Sputnik V",
            value: "SPUTNIKV",
            active: false,
        }, {
            text: "Available",
            value: "Available",
            active: false,
        }]
    }

    constructor(props) {
        super(props);
        this.OnFilterClick = this.OnFilterClick.bind(this);
    }

    OnChangeDate(date) {
        this.setState({
            week: [0 ,1, 2, 3, 4, 5, 6].map(it=> moment(date, 'YYYY-MM-DD').add(it, 'day').format("DD-MM-YY"))
        })
    }

    OnFilterClick(text) {
        this.state.filters.map(it => {
            if(it.text === text){
                it.active = !it.active;
            }
            return it;
        });
        this.setState({
            filters: this.state.filters
        });
    }

    VaccineCenter = (it) => {
        let sessions = it.sessions.filter(session => {
            return !this.state.filters.filter(filter => filter.active).some(filter => {
                return !(filter.value === session.min_age_limit ||
                    filter.value === session.vaccine ||
                    (filter.value === "Available" &&  session.available_capacity > 0))
            });
        });

        return sessions.length> 0 && <tr key={it.center_id}>
            <td>
                <div>
                    <div className={"font-weight-bolder"}>
                        {it.name}
                        <sup className={"small"}>
                            {' '}<Badge variant="secondary">{it.fee_type}</Badge>
                        </sup>
                    </div>
                    <p className={"text-secondary small m-0"}>{it.address}, {it.block_name}</p>
                    <p className={"text-secondary small m-0"}>{it.district_name}, {it.state_name}, {it.pincode}</p>
                </div>
            </td>
            {
                this.state.week.map(date => {
                    let index = sessions.findIndex(session => session.date === moment(date, 'DD-MM-YY').format('DD-MM-YYYY'));
                    if (index >= 0) {
                        return (
                            <td key={sessions[index].session_id}>
                                <div>
                                    <span className={""}>{sessions[index].vaccine}</span>
                                    <p className={"small m-0 text-danger"}>For {sessions[index].min_age_limit}+</p>
                                    {
                                        sessions[index].available_capacity ?
                                            <a href={Constant.COWIN_REGISTRATION}
                                               target="_blank" rel="noreferrer">
                                                <Button variant={"success p-1"}
                                                        data-toggle="tooltip"
                                                        data-placement="top"
                                                        title="Tooltip on top">
                                                    Book <sup><Badge
                                                    variant={"primary"}>{sessions[index].available_capacity}</Badge></sup>
                                                </Button>
                                            </a> :
                                            <Button variant={"danger p-0"}>Booked</Button>
                                    }
                                </div>
                            </td>
                        );
                    } else {
                        return (<td key={Math.random()}/>);
                    }
                })
            }
        </tr>;
    }

    render() {
        return (
            <div className={"bg-white shadow overflow-auto rounded-lg mt-2 p-0"}>
                {
                    this.props.vaccine.centers.length > 0 &&
                    this.state.filters.map(button =>{
                        if(button.active){
                            return (<Button variant={"primary m-1 no-input-shadow"} key={button.text} onClick={()=>this.OnFilterClick(button.text)}>
                                {button.text}
                            </Button>)
                        }else{
                            return (<Button variant={"outline-primary m-1 no-input-shadow"} key={button.text} onClick={()=>this.OnFilterClick(button.text)}>
                                {button.text}
                            </Button>)
                        }
                    })
                }
                {
                    this.props.vaccine.centers.length > 0 &&
                    <Table striped bordered hover>
                        <thead>
                        <tr>
                            <th>Name</th>
                            {
                                this.state.week.map(day =>
                                    <th key={"th-" + day}>{day}</th>
                                )
                            }
                        </tr>
                        </thead>
                        <tbody>
                        {
                            this.props.vaccine.centers.map(it =>
                                {
                                    return this.VaccineCenter(it)
                                }
                            )
                        }
                        </tbody>
                    </Table>
                }
                {
                    this.props.vaccine.centers.length <=0 && <div className="text-center font-weight-bold p-4 no-center-table-replacer">No centers are available for now, try to search for another pin code.</div>
                }
            </div>
        );
    }
}

const mapStateToProps = ({ user, vaccine }) => (
    {
        user: user,
        vaccine: vaccine
    }
)

const mapDispatchToProps = dispatch => ({
    setToken: token => dispatch(setToken(token))
})

export default connect(mapStateToProps, mapDispatchToProps) (VaccineSessionTable);
